function isPlaying(target) {
    let targetPos = {
        bottom: document.querySelector('#' + target).getBoundingClientRect().bottom + window.pageYOffset,
        top: window.pageYOffset + document.querySelector('#' + target).getBoundingClientRect().top
    }
    let windowPos = {
        top: window.pageYOffset,
        bottom: window.pageYOffset + document.documentElement.clientHeight
    }
    if (targetPos.bottom > windowPos.top && targetPos.top < windowPos.bottom) {
        return true;
    } else { return false; }  
}

